<template>
  <div class="music">
    <div class="music-box">
      <div class="musicheader" :style="{background: SkinStyle}">
        <div>
          <i class="iconfont icon-netease"></i>
          <span>Cloud音乐</span>
        </div>
        <div>
          <input type="search" @click="callbackPopup('公告','由于api不完整，暂未支持搜索=。=')" />
          <i class="iconfont icon-soushuo-copy"></i>
        </div>
        <div>
          <ul>
            <li class="head">
              <img src="../../public/img/20208021.jpg" alt="" />
              <span>Cloud</span>
            </li>
            <li class="skin">
              <i class="iconfont icon-zhuti1" @click="SkinShow"></i>
              <!-- 换肤 -->
            </li>
          </ul>
        </div>
        <div>
          <i class="iconfont icon-suoxiao" @click="callbackPopup('公告','该部分暂未支持=。=')"><!-- 缩小 --></i>
          <i class="iconfont icon-zuixiaohua" @click="callbackPopup('公告','该部分暂未支持=。=')"><!-- 最小化 --></i>
          <i class="iconfont icon-zuidahua" @click="callbackPopup('公告','该部分暂未支持=。=')"><!-- 最大化 --></i>
          <i class="iconfont icon-guanbi-copy" @click="callbackPopup('公告','该部分暂未支持=。=')"><!-- 关闭 --></i>
        </div>
      </div>

      <div class="musicbody" :style="{background: SkinStyle}">
        <div class="top">
          <div>
            <div class="sightcing">
              <div :class="{ rotateimg: Player.play }">
                <img src="../../public/img/music.png" alt="" />
              </div>
              <div></div>
              <div></div>
            </div>
            <div class="top-img">
              <img :src="Play.img" alt="" :class="{ running: Player.play }" />
            </div>
            <div class="top-txt">
              <ul>
                <li @click="callbackPopup('公告','该部分暂未支持=。=')">喜欢</li>
                <li @click="callbackPopup('公告','该部分暂未支持=。=')">收藏</li>
                <li @click="callbackPopup('公告','该部分暂未支持=。=')">下载</li>
                <li @click="callbackPopup('公告','该部分暂未支持=。=')">分享</li>
              </ul>
            </div>
          </div>
          <div>
            <div class="song">
              <h2>{{ Play.name }}</h2>
            </div>
            <div class="album">
              <ul>
                <li>
                  <span>专辑:</span>
                  <a>{{ Play.album }}</a>
                </li>
                <li>
                  <span>歌手:</span>
                  <a>{{ Play.singer }}</a>
                </li>
                <li>
                  <span>来源:</span>
                  <a>我喜欢的音乐</a>
                </li>
              </ul>
            </div>
            <div class="lyric">
              <ul>

                <li
                  v-for="(item, index) in Play.lryic"
                  :key="index"
                  :class="{ lryicactive: Player.lryicactive === index }"
                >
                  {{ item.txt }}
                </li>

              </ul>
            </div>
          </div>
        </div>
        <div class="bottom"></div>
      </div>

      <div class="musicfooter">
        <div class="playbutton"  :style="{background: SkinStyle}">
          <div class="play">
            <ul>
              <li @click="UpPlayFun(Play.indexlen)">
                <i class="iconfont icon-shangyishou"></i>
              </li>
              <li @click="PlaySongFun">
                <i
                  class="iconfont icon-bofang-copy"
                  :class="[
                    Player.play ? 'icon-zantingtingzhi' : 'icon-bofang-copy'
                  ]"
                ></i>
              </li>
              <li @click="NextPlayFun(Play.indexlen)">
                <i class="iconfont icon-shangyishou-copy"></i>
              </li>
            </ul>
          </div>
          <div class="time">
            <ul>
              <li>
                <span>{{ Player.timemin }}</span>
              </li>
              <li>
                <div class="slidingbox">
                  <div class="draw" :style="{ width: Player.drawpoi + '%' }">
                    <span
                      class="sliding iconfont "
                      :class="{ 'icon-xingzhuang': Player.drawload === false }"
                    ></span>
                  </div>
                </div>
              </li>
              <li>
                <span>{{ Player.timemax }}</span>
              </li>
            </ul>
          </div>
          <div class="volume">
            <i
              class="iconfont "
              :class="Player.volumepoi == 0 ? 'icon-jingyin' : 'icon-V'"
              @click="volumeMuteFun"
            ></i>
            <div class="volumebox">
              <div class="volumenbg" :style="{ width: Player.volumepoi + '%' }">
                <span class="volumesliding"></span>
              </div>
            </div>
          </div>
          <div class="list">
            <ul>
              <li
                class="iconfont"
                v-bind:class="[Player.pattern.playmode]"
                @click="patternFun"
              ></li>
              <li class="iconfont icon-yinxiaodiaojie" @click="callbackPopup('公告','该部分暂未支持=。=')"></li>
              <li class="iconfont icon-ci" @click="callbackPopup('公告','该部分暂未支持=。=')"></li>
              <li class="iconfont icon-biaozhun" @click="callbackPopup('公告','该部分暂未支持=。=')"></li>
              <li class="iconfont icon-plist" @click="listshowFun"></li>
            </ul>
          </div>
        </div>
      </div>
      <div class="musicimgbg" :style="`background-image:url(${Play.img})`"></div>
      <div class="DATA-loading">
        <div class="flower-spinner">
          <div class="dots-container">
            <div class="bigger-dot"><div class="smaller-dot"></div></div>
          </div>
        </div>
      </div>
      <div class="musicplaylist" :class="{ musiclistHight: list }">
        <div class="musicplaylist-button">
          <button
            :class="{ buttonpitch: listTableShow === 1 }"
            @click="listTableShowFun(1)"
          >
            播放列表
          </button>
          <button
            :class="{ buttonpitch: listTableShow === 2 }"
            @click="listTableShowFun(2)"
          >
            历史记录
          </button>
          <i class="iconfont icon-guanbi" @click="listshowFun"></i>
        </div>
        <div class="musicplaylist-function">
          <h3>总{{ ListLength }}首</h3>
          <button @click="callbackPopup('公告','该部分暂未支持=。=')">
            <i class="iconfont icon-shoucang" ></i>
            收藏全部
          </button>
          <span></span>
          <button>
            <i class="iconfont icon-shanchu"></i>
            清空
          </button>
        </div>
        <div class="musicplaylist-table1" >
          <!-- 播放列表1 -->
          <ul :class="{'ul-tab':listTableShow === 2}">
            <li
              v-for="(item, index) in SongList"
              :key="item.id"
              @click="playSonglistFun(index)"
              :class="{ musicplayactive: Play.indexlen === index }"
            >
              <div>
                <i
                  class="iconfont icon-bofang-copy"
                  :class="{ 'icon-zantingtingzhi': Play.indexlen === index }"
                ></i>
              </div>
              <div>
                <h3>{{ item.name }}</h3>
              </div>
              <div>
                <span>{{ item.AlName }}</span>
              </div>
              <div>
                <time>{{ item.ArName }}</time>
              </div>
            </li>
          </ul>
        </div>
        <div class="musicplaylist-table1">
          <!-- 历史列表2 -->
          <ul :class="{'ul-tab':listTableShow === 1}">
            <li v-for="item in history" :key="item.datatime">
              <div></div>
              <div>
                <h3>{{ item.name }}</h3>
              </div>
              <div>
                <span>{{ item.album }}</span>
              </div>
              <div>
                <time>{{ item.singer }}</time>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <audio
      ref="audioRef"
      :src="Play.url"
      @canplay="canplayFun"
      @onplay="SongPlaySwitch"
      @loadedmetadata="SongTimeFun"
      @timeupdate="SongPositionFun"
      @loadstart="SongLoadingFun"
      @ended="SongEndedFun"
      @pause="SongPauseSwitch"
      @volumechange="volumeSetFun"
      @canplaythrough="canplaythroughFun"
      @emptied="onemptiedFun"
    ></audio>
    <lert-window ref="LeftWindowRef" @LeftWindowColor="SkinFun" v-show="skin"></lert-window>
    <popup-window :popup="popup"></popup-window>
  </div>
</template>

<script>
import axios from "axios";
import LertWindow from "@/components/LertWindow/LertWindow";
import PopupWindow from "@/components/pop-up-window/PopupWindow";
export default {
  name: "Music",
  data() {
    return {
      SongListId: "https://autumnfish.cn/playlist/detail?id=575022767",
      SongList: [],
      list: true,
      listTableShow: 1,
      SkinStyle:"",
      skin:false,
      popup:[],
      Play: {
        name: "正在获取=。=",
        album: "正在获取=。=",
        lryic: {
          txt: "正在获取=。=",
          time: ""
        },
        img: "https://p2.music.126.net/pDRepp0KqCm8O5CRlw4EhA==/755364488304077.jpg",
        id: "1409311773",
        url: "",
        singer: "正在获取=。=",
        indexlen: 0
      },
      Player: {
        play: false,
        volume: 0.5,
        timemin: "00:00",
        timemax: "00:00",
        drawpoi: 0,
        drawload: true,
        volumepoi: "50",
        lryicactive: Number,
        timerlrc: Object,
        volumemute: {
          width: "",
          left: ""
        },
        pattern: {
          num: 0,
          playmode: "icon-danqubofang"
        }
      },
      history: []
    };
  },
  components:{
    LertWindow,
    PopupWindow
  },
  methods: {
    callbackPopup:async function(state, txt,){
      let times = await Date.now()
      this.popup.push({
        state: state,
        txt: txt,
        time:times
      });
    },
    SongListFun: function(url) {
      // 处理请求
      // let GetHistory = window.localStorage.getItem("history")
      // this.history.push(Array.of(JSON.parse(GetHistory)))
      axios
        .get(url)
        .then(async response => {
          const trackIds = response.data.playlist.trackIds;
          this.SongList.push(trackIds);
          await this.DetailsSongSetFun();
        })
        .catch(function(error) {
          if (error.response) {
            // The request was made and the server responded with a status code
            // that falls out of the range of 2xx
            console.log(error.response.data);
            console.log(error.response.status);
            console.log(error.response.headers);
          } else if (error.request) {
            // The request was made but no response was received
            // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
            // http.ClientRequest in node.js
            console.log(error.request);
          } else {
            // Something happened in setting up the request that triggered an Error
            console.log("Error", error.message);
          }
          console.log(error.config);
        });
    },
    DetailsSongSetFun: async function() {
      // 处理请求
      this.SongList = this.SongList.flat(Infinity);
      await this.SongList.forEach((item, index) => {
        axios
          .get(`https://autumnfish.cn/song/detail?ids=${item.id}`)
          .then(async response => {
            let ObjectData = {
              AlName: response.data.songs[0].al.name,
              name: response.data.songs[0].name,
              AlPicUrl: response.data.songs[0].al.picUrl,
              id: response.data.songs[0].id,
              ArName: response.data.songs[0].ar[0].name || "暂未获取"
            };
            await Object.assign(this.SongList[index], ObjectData);
          }).then(()=>{
            console.log(`数据获取完成`)
        })
          .catch(function(error) {
            if (error.response) {
              // The request was made and the server responded with a status code
              // that falls out of the range of 2xx
              console.log(error.response.data);
              console.log(error.response.status);
              console.log(error.response.headers);
            } else if (error.request) {
              // The request was made but no response was received
              // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
              // http.ClientRequest in node.js
              console.log(error.request);
            } else {
              // Something happened in setting up the request that triggered an Error
              console.log("Error", error.message);
            }
            console.log(error.config);
          });
      });
      await this.playSonglistFun(0);
      window.localStorage.setItem(
        "SONG",
        JSON.stringify(this.SongList, null, 2)
      );
    },
    SongListSetFun: async function(indexs) {
      // 处理数据对接绑定

      let URL = axios
        .get(`https://autumnfish.cn/song/url?id=${this.SongList[indexs].id}`)
        .then(async SongUrl => {
          this.SongList[indexs].URL = await SongUrl.data.data[0].url;
          this.Play.url = await this.SongList[indexs].URL;
        })
        .catch(error => {
          console.log(error);
        });
      let LRYIC = axios
        .get(`https://autumnfish.cn/lyric?id=${this.SongList[indexs].id}`)
        .then(async response => {
          const getlry = response.data.lrc.lyric;
          this.lryicFun(getlry);
        })
        .catch(() => {
          this.lryicFun(`没有获取到你想要的歌词(。・∀・)ノ`);
        });
      Promise.all([URL, LRYIC])
        .then(() => {
          this.Play.name = this.SongList[indexs].name;
          this.Play.album = this.SongList[indexs].AlName;
          this.Play.img = this.SongList[indexs].AlPicUrl;
          this.Play.id = this.SongList[indexs].id;
          this.Play.singer = this.SongList[indexs].ArName;
          this.Play.indexlen = indexs;
          let obj = Object.assign({}, this.Play);
          this.history.unshift(obj);
          this.history[0].datatime = new Date().getTime();
          let a = {};
          let b = [];
          this.history.filter(item => {
            if (!a[item.name]) {
              a[item.name] = 1;
              b.push(item);
            }
          });
          this.history = b;
          a = b = null;
        })
        .catch(e => {
          console.log(e);
        });
    },
    PlaySongFun: function(play) {
      // 播放
      if (play == 1) {
        setTimeout(() => {
          // this.Player.play = true;
        });
      }
      this.Player.play = !this.Player.play;
    },
    NextPlayFun: function(indexs) {
      // 下一首
      if (this.Player.pattern.playmode == "icon-suijibofang") {
        this.SongListSetFun(Math.floor(Math.random() * this.SongList.length));
        return this.PlaySongFun(1);
      }

      if (indexs + 1 > this.SongList.length - 1) {
        this.SongListSetFun(0);
        this.PlaySongFun(1);
      } else {
        this.SongListSetFun(indexs + 1);
        this.PlaySongFun(1);
      }
    },
    UpPlayFun: function(indexs) {
      // 上一首
      if (this.Player.pattern.playmode == "icon-suijibofang") {
        this.SongListSetFun(Math.floor(Math.random() * this.SongList.length));
        return this.PlaySongFun(1);
      }
      if (indexs - 1 <= -1) {
        this.SongListSetFun(this.SongList.length - 1);
        this.PlaySongFun(1);
      } else {
        this.SongListSetFun(indexs - 1);
        this.PlaySongFun(1);
      }
    },

    SongPauseSwitch: function() {
      // 监听停止事件
      this.Player.play = false;
    },
    SongPlaySwitch: function() {
      // 监听开始事件
      this.Player.play = true;
    },
    lryicFun: function(Str) {
      // 歌词切割
      const n = Str.split("\n");
      let array = [];
      n.forEach(item => {
        let a = item.indexOf("[");
        let b = item.indexOf("]");
        let c = item.substring(b + 1, Infinity);
        let d = item.substring(a + 1, b);
        let e = d.substring(0, d.indexOf(":"));
        let f = d.substring(d.indexOf(":") + 1, d.indexOf("."));
        let h = e * 1 * 60 + f * 1;
        let lryic = {
          txt: c,
          time: h
        };
        array.push(lryic);
      });
      this.Play.lryic = array;
    },
    SongTimeFun: function() {
      // 处理歌曲时间
      let Songlength = this.$refs.audioRef.duration;
      let x = parseInt(Songlength / 60);
      let y = parseInt(Songlength - x * 60);
      this.Player.timemax = `${x < 10 ? "0" + x : x}:${y < 10 ? "0" + y : y}`;
    },
    drawFun: function(box1, box2, box3) {
      // 鼠标滑块事件
      let switchbox = false;

      box1.addEventListener("mousedown", e => {
        switchbox = true;
        if (e.target !== box3) {
          this.Player.drawpoi = `${((e.offsetX + box3.clientWidth / 2) /
            box1.clientWidth) *
            100}`;
        }
        this.$refs.audioRef.currentTime = `${this.$refs.audioRef.duration *
          this.Player.drawpoi *
          0.01}` || 0;
        box1.addEventListener("mousemove", e => {
          let bodyX = (document.documentElement.clientWidth - 1000) /2
          if (switchbox == true) {
            this.Player.drawpoi = `${((e.pageX -
                bodyX -
              box1.offsetLeft -
              box3.clientWidth) /
              box1.clientWidth) *
              100}`;

            if (
              `${((e.offsetX + box3.clientWidth / 2) / box1.clientWidth) *
                100}` > 100
            ) {
              this.Player.drawpoi = `100`;
            }
            if (
              `${((e.offsetX + box3.clientWidth / 2) / box1.clientWidth) *
                100}` < 0
            ) {
              this.Player.drawpoi = `0`;
            }
          } else {
            return (switchbox = false);
          }
          this.$refs.audioRef.currentTime = `${this.$refs.audioRef.duration *
            this.Player.drawpoi *
            0.01}` || 0;
        });
      });
      document.documentElement.addEventListener("mouseup", () => {
        switchbox = false;
      });
      box1.addEventListener("mouseleave", () => {
        switchbox = false;
      });
    },
    SongPositionFun: function() {
      // 歌曲的播放位置变化时
      let poi = this.$refs.audioRef.currentTime;
      let x = parseInt(poi / 60);
      let y = parseInt(poi - x * 60);
      this.Player.timemin = `${x < 10 ? "0" + x : x}:${y < 10 ? "0" + y : y}`;
      this.Player.drawpoi = (poi / this.$refs.audioRef.duration) * 100;
      this.Play.lryic.forEach((item, index) => {
        if (item.time == Math.round(poi)) {
          if(item.time ===0){
            this.Player.lryicactive = 0;
          }else{
            this.Player.lryicactive = index;
            this.LryicScrollFun();
          }
        }
      });
    },
    LryicScrollFun: function() {
      // 歌词滚动
      clearTimeout(this.Player.timerlrc);
      let ul = document.querySelector(".lyric>ul");
      this.Player.timerlrc = setTimeout(() => {
        let ost = document.querySelector(".lryicactive").offsetTop;
        if (0 === Math.floor((ost - ul.scrollTop - ul.offsetHeight / 2) / 10)) {
          return clearTimeout(this.Player.timerlrc);
        }
        if (ost < ul.offsetHeight && ul.scrollTop < ost) {
          return clearTimeout(this.Player.timerlrc);
        }
        if (
          ul.scrollHeight - ul.offsetHeight === ul.scrollTop &&
          ost > ul.scrollTop
        ) {
          return clearTimeout(this.Player.timerlrc);
        }
        ul.scrollTop += Math.floor(
          (ost - ul.scrollTop - ul.offsetHeight / 2) / 10
        );
        this.LryicScrollFun();
      }, 10);
    },
    SongLoadingFun: function() {
      this.Player.drawload = false;
    },
    volumenFun: function(box1, box2, box3) {
      // 音量调节
      let switchbox = false;
      box1.addEventListener("mousedown", e => {
        let bodyX = (document.documentElement.clientWidth - 1000) /2
        switchbox = true;
        if(e.target!==box3) {
          this.Player.volumepoi = `${(e.offsetX + box3.clientWidth / 2) / box1.clientWidth * 100}`;
        }
        box1.addEventListener("mousemove", e => {
            if(switchbox == true){
                this.Player.volumepoi = `${(e.pageX - bodyX - box1.offsetLeft-box3.clientWidth/2)/box1.clientWidth *100}`;
              if(`${(e.offsetX + box3.clientWidth/2)/box1.clientWidth *100}` > 100){
                this.Player.volumepoi = `100`
              }
              if(`${(e.offsetX + box3.clientWidth/2)/box1.clientWidth *100}` < 0){
                this.Player.volumepoi = `0`
              }
          } else {
            return (switchbox = false);
          }
        });
      });
      document.documentElement.addEventListener("mouseup", () => {
        switchbox = false;
      });
      box1.addEventListener("mouseleave", () => {
        switchbox = false;
      });
    },
    volumeSetFun: function() {
      this.Player.volumepoi = this.$refs.audioRef.volume * 100;
    },
    volumeMuteFun: function() {
      // 静音调节
      if (this.Player.volumepoi !== 0) {
        this.Player.volumemute.width = this.Player.volumepoi;
      }
      if (this.Player.volumepoi == 0) {
        this.Player.volumepoi = this.Player.volumemute.width;
      } else {
        this.$refs.audioRef.volume = 0;
      }
    },
    patternFun: function() {
      // 模式调节
      this.Player.pattern.num++;
      if (this.Player.pattern.num >= 4) {
        this.Player.pattern.num = 0;
      }
      switch (this.Player.pattern.num) {
        case 0:
          this.Player.pattern.playmode = "icon-danqubofang";
          break;
        case 1:
          this.Player.pattern.playmode = "icon-liebiaoxunhuan";
          break;
        case 2:
          this.Player.pattern.playmode = "icon-suijibofang";
          break;
        case 3:
          this.Player.pattern.playmode = "icon-danquxunhuan";
          break;
        default:
          break;
      }
    },
    PlayState: function() {
      // 下一首应该怎么播放
      switch (this.Player.pattern.playmode) {
        case "icon-danqubofang":
          break;
        case "icon-liebiaoxunhuan":
          this.NextPlayFun(this.Play.indexlen);
          break;
        case "icon-suijibofang":
          this.NextPlayFun(Math.floor(Math.random() * this.SongList.length));
          break;
        case "icon-danquxunhuan":
          this.NextPlayFun(this.Play.indexlen - 1);
          break;
        default:
          break;
      }
    },
    listshowFun: function() {
      this.list = !this.list;
    },
    canplaythroughFun: function() {
      // alert("可以播放了")
      this.Player.drawload = true;
      this.Player.play = true;
    },
    SongEndedFun: function() {
      this.PlayState();
    },
    canplayFun: function() {
      this.$refs.audioRef.play();
    },
    playSonglistFun: function(index) {
      // 点击列表播放
      this.SongListSetFun(index);
      this.PlaySongFun(1);
    },
    listTableShowFun: function(index) {
      index === 1 ? (this.listTableShow = 1) : (this.listTableShow = 2);
    },
    onemptiedFun:function (){
      console.log(`onemptiedFun`)
    },
    SkinFun(Color){
      this.SkinStyle = Color
    },
    SkinShow(){
      this.skin = !this.skin
    }
  },

  watch: {
    "Player.play": {
      handler() {
        if (this.Player.play == true) {
          return this.$refs.audioRef.play();
        } else {
          return this.$refs.audioRef.pause();
        }
      }
    },
    "Player.volumepoi": {
      handler() {
        return (this.$refs.audioRef.volume = this.Player.volumepoi / 100);
      }
    }
  },
  mounted() {
    setTimeout( () => {
      this.SongListFun(this.SongListId);
    }, 40);
    const slidingbox = document.querySelector(".slidingbox"),
      draw = document.querySelector(".draw"),
      volumebox = document.querySelector(".volumebox"),
      sliding = document.querySelector(".sliding"),
      volumenbg = document.querySelector(".volumenbg"),
      volumesliding = document.querySelector(".volumesliding");
    this.drawFun(slidingbox, draw, sliding);
    this.volumenFun(volumebox, volumenbg, volumesliding);
    console.log(this.$refs)
  },
  computed:{
    ListLength: function () {
      if(this.listTableShow === 1){
        return this.SongList.length
      }else{
        return this.history.length
      }
    }
  },
  destroyed() {
    this.drawFun = null;
    this.volumenFun = null;
  }
};
</script>

