<template>
  <div class="lertwindowbox" ref="leftwindowref">
    <div class="lert-window">
      <canvas ref="colorref" class="canvas-color"></canvas>
      <h3>主题</h3>
      <br />
      <ul>
        <li
          v-for="item in color"
          :key="item"
          :style="{ backgroundColor: item }"
          @click="PitchColorFun(item)"
        >
          <i class="iconfont icon-xuanzhong" v-show="pitch === item"></i>
        </li>
      </ul>
      <br />
      <h3>自定义颜色</h3>
      <br />
      <div class="colorrange">
        <input type="range" min="0" max="360" ref="rgb" @change="OffColor" /><br />
        <input type="range" min="0" max="100" ref="rgba"  @change="OffColor"/>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "LertWindow",
  data() {
    return {
      color: [
        "#ff5c8a",
        "#ff7a9e",
        "#fe76c8",
        "#717ff9",
        "#4791eb",
        "#39afea",
        "#2bb669",
        "#6acc19",
        "#e2ab12",
        "#ff8f57",
        "#fd726d",
        "#fd544e"
      ],
      pitch: Object,
      Color: `rgb(255 0 0)`,
    };
  },
  methods: {
    PitchColorFun(item) {
      this.Color = `${item}`;
      this.RgbColorFun(this.Color);
      this.$emit('LeftWindowColor',this.Color)
      return (this.pitch = item);
    },
    RgbColorFun: function (Color) {
      let GetColor = Color || `rgb(255 0 0)`;
      let Width = this.$refs.colorref.offsetWidth;
      let Height = this.$refs.colorref.offsetHeight
      let ctx = this.$refs.colorref.getContext("2d");
      let gradientBase = ctx.createLinearGradient(0, 0, Width, 0);
      gradientBase.addColorStop(1, GetColor);
      gradientBase.addColorStop(0, "rgba(255,255,255,1)");
      ctx.fillStyle = gradientBase;
      ctx.fillRect(0, 0, Width, Height);
      // 第二次填充，黑色到透明
      let my_gradient1 = ctx.createLinearGradient(0, 0, 0, Height);
      my_gradient1.addColorStop(0, "rgba(0,0,0,0)");
      my_gradient1.addColorStop(1, "rgba(0,0,0,1)");
      ctx.fillStyle = my_gradient1;
      ctx.fillRect(0, 0, Width, Height);
    },
    SlideValue: function () {
      let switches = false;
      this.$refs.rgb.addEventListener("mousedown", () => {
        switches = true;
        this.GetAngle()
        this.$refs.rgb.addEventListener("mousemove", () => {
            if (switches === true) {
              this.GetAngle()
            } else {
              return switches = false;
            }
        })
      })
      document.documentElement.addEventListener("mouseup", () => {
        switches = false;
      })
    },
    SlideValueA: function () {
      let switches = false;
      this.$refs.rgba.addEventListener("mousedown", () => {
        switches = true;
        this.GetAngle()
        this.$refs.rgba.addEventListener("mousemove", () => {
          if (switches === true) {
            this.GetAngle()
          } else {
            return switches = false;
          }
        })
      })
      document.documentElement.addEventListener("mouseup", () => {
        switches = false;
      })
    },
    GetAngle(){
      let Deg = this.$refs.rgb.value+"deg";
      this.Color = `hsl(${Deg} 100% 50%/${this.$refs.rgba.value/100})`;
      this.RgbColorFun(this.Color);
      this.$emit('LeftWindowColor',this.Color)
    },
    OffColor(){
      this.pitch = null
    }
  },
  mounted() {
    this.RgbColorFun(this.Color);
    this.SlideValue()
    this.SlideValueA()
  }
};
</script>

<style scoped lang="less">
.lertwindowbox {
  position: fixed;
  left: 50%;
  top: 25%;
  transform: translate(-50%);
  z-index: 20;

  .lert-window {
    background-color: #fff;
    padding: 2em;
    position: relative;
    background: transparent;
    .canvas-color {
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      z-index: -1;
    }
    h3 {
      text-align: center;
    }
    ul {
      display: flex;
      display: flex;
      justify-content: center;
      width: 430px;
      flex-wrap: wrap;
      li {
        width: 40px;
        height: 40px;
        cursor: pointer;
        margin: 1em;
      }
    }
    .colorrange {
      input {
        width: 100%;
        -webkit-appearance: none;
      }
      input[type="range"]:nth-of-type(1) {
        background: linear-gradient(
          to right,
          rgb(255 0 0),
          rgb(255 255 0),
          rgb(0 255 0),
          rgb(0 255 255),
          rgb(0 0 255),
          rgb(255 0 255)
        );
      }
      input[type="range"]:nth-of-type(2) {
        background: linear-gradient(
          to right,
          rgba(255, 0, 0, 1),
          rgba(255, 0, 0, 0.9),
          rgba(255, 0, 0, 0.8),
          rgba(255, 0, 0, 0.7),
          rgba(255, 0, 0, 0.6),
          rgba(255, 0, 0, 0.5),
          rgba(255, 0, 0, 0.4),
          rgba(255, 0, 0, 0.3),
          rgba(255, 0, 0, 0.2),
          rgba(255, 0, 0, 0.1),
          rgba(255, 0, 0, 0)
        );
      }
    }
  }
}
</style>
